/*
 * Denna fil inneh√•ller allm√§nna verktygsfunktioner som anv√§nds i flera delar av applikationen.
 * Funktioner inkluderar att v√§xla navigeringsmenyn, visa snackbars och sanera inmatade v√§rden.
 */

// Funktion f√∂r att v√§xla navigeringsmenyn p√• sm√• sk√§rmar
export function myFunction() {
    var x = document.getElementById("myTopnav");
    if (x.className === "topnav") {
        x.className += " responsive";
    } else {
        x.className = "topnav";
    }
}

// Funktion f√∂r att visa en snackbar med ett meddelande
export function showSnackbar(message) {
    var snackbar = document.getElementById("snackbar");
    snackbar.textContent = message;
    snackbar.className = "show";
    setTimeout(function(){ snackbar.className = snackbar.className.replace("show", ""); }, 3000);
}

// Funktion f√∂r att sanera inmatade v√§rden f√∂r att undvika XSS-attacker
export function sanitizeInput(input) {
    return input ? input.replace(/(<([^>]+)>)/ig, '') : '';
}
